<script>
    export default {
        name: 'Aside',
        props: {
            active: String
        },
        mounted: function() {
            this.setActive() //method will execute at pageload
        },
        methods: {
            setActive: function() {
                let svgs = document.querySelectorAll("#link path");
                let links = document.querySelectorAll("#link");
                switch(this.active) {
                    case 'Главная': {
                        links[0].classList.add('active');
                        svgs[0].style.stroke = "#9677F1";
                        break;
                    }
                    case 'Обучение': {
                        links[1].classList.add('active');
                        svgs[1].style.stroke = "#9677F1";
                        svgs[2].style.stroke = "#9677F1";
                        svgs[3].style.fill = "#9677F1";
                        break;
                    }
                    case 'Справочник': {
                        links[2].classList.add('active');
                        svgs[4].style.stroke = "#9677F1";

                        break;
                    }
                }
            }
        }
    }
</script>

<style>
    .nav {
        float: left;
        width: 270px;
    }

    .nav a {
        font-family: "Montserrat", sans-serif;
        font-size: 18px;
        font-weight: 600;
        color: #6D7188;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    }

    .nav span {
        padding-left: 16px;
    }

    .nav ul {
        padding-left: 75px;
        margin-top: 46px;
        list-style: none;
        position: fixed;
    }

    .nav .active {
        color: #9677F1;
    }

    .nav li {
        border-radius: 7px;
        margin-top: 16px;
        padding: 8px;
        width: 232px;
        transition: 0.15s ease-in-out;
    }

    .nav li:hover {
        background: rgba(0,0,0, 0.02);
    }

    .nav li:first-child {
        margin-top: 0;
    }
    
    .active {
        color: #9677F1;
    }

    .nav-icon {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
    }

    @media screen and (max-width: 1800px) {
        .nav span , .nav hr {
            display: none;
        }

        .nav li {
          width: auto;
        }
    }
</style>